/* 招募申请者管理页面样式 */
.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
}

.applicantsTable {
  width: 100%;
  border-collapse: collapse;
}

.backButton {
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  background-color: #6c757d;
  color: white;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: 0 2px 4px rgba(108, 117, 125, 0.1);
  display: inline-block;
  margin-bottom: 20px;
}

.backButton:hover {
  background-color: #5a6268;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(108, 117, 125, 0.15);
}

