/* 招募创建页面样式 */
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.formGroup {
  margin-bottom: 20px;
}

.submitButton {
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  background-color: #4a6cf7;
  color: white;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: 0 2px 4px rgba(74, 108, 247, 0.1);
  margin-top: 20px;
}

.submitButton:hover {
  background-color: #3a5bdf;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(74, 108, 247, 0.15);
}

.submitButton:disabled {
  background-color: #a5b4fc;
  cursor: not-allowed;
  box-shadow: none;
  transform: none;
}

