.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 30px;
  background-color: white;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  margin-top: 30px;
}

.options {
  margin: 15px 0;
  padding: 16px;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  transition: border-color 0.2s ease;
}

.options:focus-within {
  border-color: #007bff;
}

.optionLabel {
  display: flex;
  align-items: center;
  margin: 10px 0;
  padding: 12px;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.optionLabel:hover {
  background-color: #f8f9fa;
}

.optionLabel input {
  margin-right: 12px;
  width: 18px;
  height: 18px;
}

.questionText {
  margin: 24px 0 16px;
  color: #212529;
  font-size: 18px;
  font-weight: 500;
}

.textInput {
  width: 100%;
  padding: 14px;
  margin: 10px 0;
  border: 1px solid #ced4da;
  border-radius: 8px;
  min-height: 120px;
  font-size: 16px;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.textInput:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.1);
}

.backButton, .submitButton {
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  transition: all 0.2s ease;
}

.backButton {
  background-color: #6c757d;
  color: white;
  box-shadow: 0 2px 4px rgba(108, 117, 125, 0.1);
  margin-right: 10px;
}

.backButton:hover {
  background-color: #5a6268;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(108, 117, 125, 0.15);
}

.submitButton {
  background-color: #4a6cf7;
  color: white;
  box-shadow: 0 2px 4px rgba(74, 108, 247, 0.1);
}

.submitButton:hover {
  background-color: #3a5bdf;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(74, 108, 247, 0.15);
}

